.booksContainer {
  padding-left: 35px;
  width: 95%;
  display: grid;
  grid-template-columns: repeat(3, 30%);
  gap: 20px 3%
}

@media (max-width: 960px) {
  .booksContainer {
    grid-template-columns: repeat(2, 45%);
  }
}


@media (max-width: 680px) {
  .booksContainer {
    grid-template-columns: repeat(1, 80%);
  }
}