.generator {
  margin-top: 10px;
  outline: none;
  width: 200px;
  height: 50px;
  border: 1px solid rgba(0, 0, 0, 0.433);
  cursor: pointer;
  background-color: transparent;
  font-size: 1.2rem;
  transition: all ease-in-out 0.5s;
}

.generator:hover {
  scale: 1.1;
  box-shadow: 0px 1px 9px -3px rgba(39, 39, 39, 1);
}