.book-viewer-wrapper {
  display: none;
}

.book-viewer-wrapper-opened{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.748);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.book-viewer-wrapper-opened .book-container {
  max-height: 90%;
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  justify-content: center;
  padding: 30px 100px;
  background-color: rgba(0, 0, 0, 0.696);
  box-shadow: 0px 1px 39px -3px rgb(255, 255, 255);
  border-radius: 15px;
  animation: bounce-in 1.5s;
  position: relative;
}

.book-container h2, .book-author, .book-rating{
  color: rgba(255, 255, 255, 0.774);
  margin: 10px;
}

.book-container button{
  position: absolute;
  top: 0;
  right: -60px;
  margin-bottom: 20px;
  color: rgba(255, 255, 255, 0.652);
  border-color: transparent;
}

.book-container button:hover{
  box-shadow: none;
}

.book-container p.error {
  font-size: 1.5rem;
  color: rgba(255, 255, 255, 0.774);
}

.book-container .desc-link {
  color: rgba(255, 255, 255, 0.774);
  transition: all 0.5s ease-in-out;
}

.book-container .desc-link:hover {
  cursor: pointer;
  scale: 1.2;
}

@keyframes bounce-in {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}


@media (max-width: 960px) {
  .book-viewer-wrapper-opened .book-container{
    width: 60%;
    max-height: 80%;
  }
}

