.bookPageWrapper {
  width: 80%;
  margin: 0 auto;
  padding: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid rgba(0, 0, 0, 0.433);
  box-shadow: 0px 1px 9px -3px rgba(39, 39, 39, 1);
  border-radius: 14px;
}

.bookPageWrapper .title {
  font-size: 2rem;
}

.bookPageWrapper .info {
  width: 100%;
  color: rgba(0, 0, 0, 0.433);
  display: flex;
  justify-content: space-evenly;
  margin-bottom: 50px;
}

.bookPageWrapper .bookDescription {
  display: flex;
  justify-content: space-evenly;
  margin-bottom: 50px;
}

.bookPageWrapper .bookDescription .fullDescription {
  width: 40%;
}

.bookPageWrapper .genres {
  text-align: center;
}

@media (max-width: 680px) {
  .bookPageWrapper .bookDescription {
    flex-direction: column;
    align-items: center;
    margin-bottom: 10px;
  }

  .bookPageWrapper .title{
    font-size: 1rem;
  }

  .bookPageWrapper .info{
    margin-bottom: 10px;
  }

  .bookPageWrapper .bookDescription img {
    width: 80%;
    margin-bottom: 10px;
  }

  .bookPageWrapper .bookDescription .fullDescription {
    width: 80%;
  }

}